.cards__admin {
    padding: 4rem;
    background: #fff;
  }
  
  h1 {
    text-align: center;
  }
  
  .cards__container__admin {
    display: flex;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards__wrapper__admin {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items__admin {
    margin-bottom: 24px;
    display: inline;
  }
  
  .cards__item__admin {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link__admin {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
    background-color: #eedfd5;
  }
  
  .cards__item__pic-wrap__admin {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img__admin {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
 
  
  .cards__item__img__admin {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img__admin:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info__admin {
    padding: 20px 30px 30px;
    background-color: #eedfd5;
  }
  
  .cards__item__text__admin {
    color: #482c25;
    font-size: 18px;
    line-height: 20px;
    text-align: center;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }